<template>
  <div class="register">
    <el-card class="box-card">
      <div slot="header" class="clearfix">
        <span class="title"></span>
        <el-button style="float: right; padding: 3px 0" type="text" @click="toLogin"></el-button>
      </div>
      <div class="text item">
        <el-form
          :model="registerForm"
          status-icon
          :rules="registerRules"
          ref="registerForm"
          label-width="100px"
        >
          <el-form-item label="" prop="username">
            <el-input type="text" v-model="registerForm.username"></el-input>
          </el-form-item>
          <el-form-item label="" prop="identity">
            <el-select v-model="registerForm.identity" placeholder="">
              <el-option label="" value="manager" disabled></el-option>
              <el-option label="" value="employee"></el-option>
            </el-select>
          </el-form-item>
          <el-form-item label="" prop="key">
            <el-input type="text" v-model="registerForm.key"></el-input>
          </el-form-item>
          <el-form-item label="" prop="password">
            <el-input type="password" v-model="registerForm.password"></el-input>
          </el-form-item>
          <el-form-item label="" prop="password2">
            <el-input type="password" v-model="registerForm.password2"></el-input>
          </el-form-item>
          <el-form-item>
            <el-button class="btn" type="primary" @click="submitForm('registerForm')"></el-button>
          </el-form-item>
        </el-form>
      </div>
    </el-card>
  </div>
</template>

<script>
export default {
  name: 'register',
  data() {
    const validatePassword2 = (rule, value, callback) => {
      if (value !== this.registerForm.password) {
        callback(new Error('!'));
      } else {
        callback();
      }
    };
    return {
      registerForm: {
        username: '',
        password: '',
        password2: '',
        identity: '',
        key: ''
      },
      registerRules: {
        username: [
          { required: true, message: '', trigger: 'blur' },
          {
            pattern: /^[a-zA-Z0-9_-]{4,16}$/,
            message: '416',
            trigger: 'blur'
          }
        ],
        key: [
          { required: true, message: '', trigger: 'blur' },
          {max: 20, message: '', trigger:'blur'}
        ],
        password: [
          { required: true, message: '', trigger: 'blur' },
          {
            pattern: /^[A-Za-z0-9]{4,12}$/,
            message: '612',
            trigger: 'blur'
          }
        ],
        password2: [
          { required: true, message: '', trigger: 'blur' },
          { validator: validatePassword2, trigger: 'blur' }
        ],
        identity: [{ required: true, message: '', trigger: 'blur' }]
      }
    };
  },
  methods: {
    submitForm(formName) {
      this.$refs[formName].validate(valid => {
        if (valid) {
          this.$axios.post('/users/register', this.registerForm).then(res => {
            this.$message({
              type: 'success',
              message: res.data
            });
            this.$router.push('/login');
          });
        } else {
          this.$message({
            type: 'error',
            message: ''
          });
          return false;
        }
      });
    },
    toLogin() {
      this.$router.push('/login');
    }
  }
};
</script>

<style scopde>
.register {
  width: 100%;
  height: 100vh;
  background: url('../assets/bg.jpg') no-repeat fixed;
  background-size: cover;
}
