<template>
  <div class="userlist">
    <el-table :data="users" border style="width: 100%">
      <el-table-column type="index" width="100" align="center"></el-table-column>
      <el-table-column prop="date" :formatter="formatDate" label="" align="center"></el-table-column>
      <el-table-column prop="username" label="" align="center"></el-table-column>
      <el-table-column prop="identity" label="" align="center">
        <template slot-scope="scope">
          <el-tag
            :type="scope.row.identity == 'manager' ? 'warning' : 'success'"
            size="medium"
          >{{scope.row.identity == 'manager' ? '' : ''}}</el-tag>
        </template>
      </el-table-column>
      <el-table-column label="" align="center">
        <template slot-scope="scope">
          <el-button size="small" @click="changeIdentity(scope.$index, scope.row)"></el-button>
          <el-button size="small" type="danger" @click="handleDelete(scope.$index, scope.row)"></el-button>
        </template>
      </el-table-column>
    </el-table>
    <el-dialog title="" :visible.sync="showDialog" :before-close="beforeCloseDialog">
      <el-form :model="userForm" ref="userForm" :rules="userRules">
        <el-form-item label="" label-width="80px" prop="username">
          <el-input v-model="userForm.username"></el-input>
        </el-form-item>
        <el-form-item label="" label-width="80px">
          <el-select v-model="userForm.identity" placeholder="">
            <el-option label="" value="manager"></el-option>
            <el-option label="" value="employee"></el-option>
          </el-select>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button @click="cancelDialog"> </el-button>
        <el-button type="primary" @click="editUser('userForm')"> </el-button>
      </div>
    </el-dialog>
  </div>
</template>

<script>
import facha from 'fecha';
export default {
  name: 'userlist',
  data() {
    return {
      users: [],
      showDialog: false,
      userForm: {},
      userRules: {
        username: [
          { required: true, message: '', trigger: 'blur' },
          {
            pattern: /^[a-zA-Z0-9_-]{4,16}$/,
            message: '416',
            trigger: 'blur'
          }
        ]
      }
    };
  },
  methods: {
    // 
    formatDate(row, column, cellValue, index) {
      const date = facha.format(new Date(cellValue), 'YYYY-MM-DD hh:mm:ss ZZ');
      return date;
    },
    // 
    changeIdentity(index, row) {
      this.showDialog = true;
      this.userForm = row;
    },
    // 
    handleDelete(index, row) {
      this.$confirm(`${row.username}?`, '', {
        confirmButtonText: '',
        cancelButtonText: '',
        type: 'warning'
      })
        .then(() => {
          this.$axios.delete(`/users/${row._id}`).then(res => {
            this.$message({
              type: 'success',
              message: res.data
            });
            this.fetchUserlist();
          });
        })
        .catch(() => {
          this.$message({
            type: 'info',
            message: ''
          });
        });
    },
    // 
    fetchUserlist() {
      this.$axios.get('/users').then(res => {
        this.users = res.data;
      });
    },
    // 
    editUser() {
      this.$refs['userForm'].validate(valid => {
        if (valid) {
          this.$axios
            .put(`/users/${this.userForm._id}`, this.userForm)
            .then(res => {
              this.$message({
                type: 'success',
                message: res.data
              });
              this.showDialog = false;
            });
        } else {
          this.$message.error('');
          return false;
        }
      });
    },
    // dialoguserlist
    beforeCloseDialog(done) {
      this.fetchUserlist();
      done();
    },
    cancelDialog() {
      this.showDialog = false;
      this.fetchUserlist();
    }
  },
  created() {
    this.fetchUserlist();
  }
};
</script>
