<template>
  <el-dialog :title="dialog.title" :visible.sync="dialog.show" @open="beforeDialogOpen">
    <el-form :model="profileForm" ref="profileForm" :rules="profileRules">
      <el-tabs tab-position="left">
        <el-tab-pane label="">
          <el-form-item prop="date" label="" label-width="80px">
            <el-date-picker
              value-format="yyyy-MM-dd"
              v-model="profileForm.date"
              type="date"
              placeholder=""
            ></el-date-picker>
          </el-form-item>
          <el-form-item prop="isMarket" label="" label-width="80px">
            <el-radio-group v-model="profileForm.isMarket">
              <el-radio :label="false" border size="small"></el-radio>
              <el-radio :label="true" border size="small"></el-radio>
            </el-radio-group>
          </el-form-item>
          <el-form-item prop="duckCount" label="" label-width="80px">
            <el-input
              type="number"
              v-model.number="profileForm.duckCount"
              placeholder=""
              clearable
            >
              <template slot="append"></template>
            </el-input>
          </el-form-item>
        </el-tab-pane>
        <el-tab-pane label="">
          <el-form-item prop="income" label="" label-width="80px">
            <el-input
              type="number"
              v-model.number="profileForm.income"
              placeholder=""
              clearable
            ></el-input>
          </el-form-item>
          <el-form-item prop="foodsExpend" label="" label-width="80px">
            <el-input
              type="number"
              v-model.number="profileForm.foodsExpend"
              placeholder=""
              clearable
            ></el-input>
          </el-form-item>
          <el-form-item prop="frozenExpend" label="" label-width="80px">
            <el-input
              type="number"
              v-model.number="profileForm.frozenExpend"
              placeholder=""
              clearable
            ></el-input>
          </el-form-item>
          <el-form-item prop="otherExpend" label="" label-width="80px">
            <el-input
              type="number"
              v-model.number="profileForm.otherExpend"
              placeholder=""
              clearable
            ></el-input>
          </el-form-item>
          <el-form-item prop="profit" label="" label-width="80px">
            <el-input type="number" v-model.number="profit" placeholder="" clearable></el-input>
          </el-form-item>
        </el-tab-pane>
        <el-tab-pane label="">
          <el-form-item prop="remark" label="" label-width="60px">
            <el-input
              type="textarea"
              :rows="4"
              v-model.number="profileForm.remark"
              placeholder=""
              clearable
            ></el-input>
          </el-form-item>
        </el-tab-pane>
      </el-tabs>
    </el-form>
    <div slot="footer" class="dialog-footer">
      <el-button @click="dialog.show = false"> </el-button>
      <el-button type="primary" @click="submitForm('profileForm')"> </el-button>
    </div>
  </el-dialog>
</template>

<script>
export default {
  name: 'profile-dialog',
  data() {
    return {
      profileRules: {
        date: [
          {
            type: 'string',
            required: true,
            message: '',
            triggle: 'blur'
          }
        ],
        isMarket: [
          {
            type: 'boolean',
            required: true,
            message: '',
            triggle: 'blur'
          }
        ],
        duckCount: [
          {
            type: 'number',
            required: true,
            message: '',
            triggle: 'blur'
          }
        ],
        income: [
          {
            type: 'number',
            required: true,
            message: '',
            triggle: 'change'
          }
        ],
        foodsExpend: [
          {
            type: 'number',
            message: '',
            triggle: 'change'
          }
        ],
        frozenExpend: [
          {
            type: 'number',
            message: '',
            triggle: 'change'
          }
        ],
        otherExpend: [
          {
            type: 'number',
            message: '',
            triggle: 'change'
          }
        ],
        profit: [
          {
            type: 'number',
            required: true,
            message: '',
            triggle: 'blur'
          }
        ]
      }
    };
  },
  computed: {
    profileForm: {
      get: function() {
        return this.profile;
      },
      set: function(val) {}
    },
    profit: {
      get: function() {
        return (
          this.profileForm.income -
          this.profileForm.foodsExpend -
          this.profileForm.frozenExpend -
          this.profileForm.otherExpend
        );
      },
      set: function(val) {}
    }
  },
  props: {
    dialog: Object,
    profile: Object
  },
  methods: {
    submitForm(formName) {
      this.$refs[formName].validate(valid => {
        if (valid) {
          this.profileForm.profit = this.profit;
          if (this.dialog.option == 'add') {
            this.$axios.post(`/profiles/add`, this.profileForm).then(res => {
              this.$message.success(res.data);
              this.dialog.show = false;
              this.$emit('update');
            });
          } else {
            this.$axios
              .put(`/profiles/edit/${this.profile._id}`, this.profileForm)
              .then(res => {
                this.$message.success(res.data);
                this.dialog.show = false;
                this.$emit('update');
              });
          }
        } else {
          this.$message.error('');
        }
      });
    },
    beforeDialogOpen() {
      if (this.dialog.option == 'add' && this.$refs['profileForm']) {
        this.$refs['profileForm'].resetFields();
      }
    }
  }
};
</script>
