<template>
  <div class="profitlist">
    <el-row style="margin-bottom: 10px">
      <el-col :span="24">
        <el-date-picker value-format="yyyy" v-model="queryYear" type="year" placeholder=""></el-date-picker>
        <el-button
          @click="queryData"
          type="primary"
          icon="el-icon-search"
          size="medium"
          style="margin-left:20px"
        ></el-button>
      </el-col>
    </el-row>
    <el-table :data="profitList" style="width: 100%" border>
      <el-table-column prop="_id" :label="year + ''" align="center">
        <template slot-scope="scope">
          <span style="color: #304455">{{scope.row._id}}</span>
        </template>
      </el-table-column>
      <el-table-column prop="totalDuckCount" label="" align="center">
        <template slot-scope="scope">
          <span style="color: #E6A23C">{{scope.row.totalDuckCount}}</span>
        </template>
      </el-table-column>
      <el-table-column prop="totalIncome" label="" align="center">
        <template slot-scope="scope">
          <span style="color: #409EFF">{{scope.row.totalIncome}}</span>
        </template>
      </el-table-column>
      <el-table-column prop="totalFoods" label="" align="center">
        <template slot-scope="scope">
          <span style="color: #F56C6C">{{scope.row.totalFoods}}</span>
        </template>
      </el-table-column>
      <el-table-column prop="totalFrozen" label="" align="center">
        <template slot-scope="scope">
          <span style="color: #F56C6C">{{scope.row.totalFrozen}}</span>
        </template>
      </el-table-column>
      <el-table-column prop="totalOther" label="" align="center">
        <template slot-scope="scope">
          <span style="color: #F56C6C">{{scope.row.totalOther}}</span>
        </template>
      </el-table-column>
      <el-table-column prop="totalProfit" label="" align="center">
        <template slot-scope="scope">
          <span style="color: #67C23A">{{scope.row.totalProfit}}</span>
        </template>
      </el-table-column>
    </el-table>
  </div>
</template>

<script>
export default {
  name: 'profitlist',
  data() {
    return {
      profitList: [],
      year: '',
      queryYear: ''
    };
  },
  methods: {
    getIncome() {
      const date = new Date();
      this.year = `${date.getFullYear()}`;
      this.$axios.get('/income', { params: { year: this.year } }).then(res => {
        this.profitList = res.data;
      });
    },
    queryData() {
      if (this.queryYear) {
        this.$axios
          .get('/income', { params: { year: this.queryYear } })
          .then(res => {
            this.profitList = res.data;
          });
      } else {
        this.getIncome();
      }
    }
  },
  created() {
    this.getIncome();
  }
};
</script>

